
















import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class NavComponent extends Vue {
  @Prop({
    type: String,
    default: ''
  })
  value: any | string;

  get search() {
    return this.value;
  }
  set search(v) {
    this.$emit('input', v);
    this.value = v;
  }
}
